import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { getDatabase, ref, get } from 'firebase/database';
import { onAuthStateChanged } from 'firebase/auth';
import { auth, database } from './firebase';
import PropertyForm from './PropertyForm';
import Propertylist from './Propertylist';
import Booking from './Booking';

function DashboardPage() {
  const [isPropertyFormOpen, setIsPropertyFormOpen] = useState(false);
  const [userRole, setUserRole] = useState(null);
  const navigate = useNavigate();

  useEffect(() => {
    const checkUserRole = async () => {
      const user = auth.currentUser;

      if (user) {
        const userRef = ref(database, `users/${user.uid}`);
        const snapshot = await get(userRef);

        if (snapshot.exists()) {
          const userData = snapshot.val();
          setUserRole(userData.role);

          if (userData.role !== 'property_owner') {
            navigate('/');
          }
        } else {
          navigate('/');
        }
      } else {
        navigate('/');
      }
    };

    checkUserRole();
  }, [navigate]);

  const openPropertyForm = () => {
    setIsPropertyFormOpen(true);
    console.log(isPropertyFormOpen);
  };

  const closePropertyForm = () => {
    setIsPropertyFormOpen(false);
  };

  return (
    <div>
      <div className="d-flex flex-row-reverse">
        <button onClick={openPropertyForm} data-toggle="modal" data-target="#exampleModal"> </button>
      </div>
      {isPropertyFormOpen && <PropertyForm />}
      <Propertylist deleteo={true} />
      <Booking deleteo={true} />
    </div>
  );
}

export default DashboardPage;

