import React, { useEffect, useState } from 'react';
import { getDatabase, ref, get, update } from 'firebase/database';

function Userdata({ userId }) {
  const [userData, setUserData] = useState({
    fullName: '',
    address: '',
    email: '',
    phoneNumber: '',
    role: '',
  });
  const [isEditing, setIsEditing] = useState(false);

  useEffect(() => {
    const fetchUserData = async () => {
      const database = getDatabase();
      const userRef = ref(database, `users/${userId}`);
      const userSnapshot = await get(userRef);

      if (userSnapshot.exists()) {
        setUserData(userSnapshot.val());
      }
    };

    if (userId) {
      fetchUserData();
    }
  }, [userId]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setUserData((prevData) => ({
      ...prevData,
      [name]: value,
    }));
  };
  const handleUpdate = async () => {
    try {
      const { role, ...dataToUpdate } = userData; // Exclude role from the data to update
      const database = getDatabase();
      const userRef = ref(database, `users/${userId}`);
      await update(userRef, dataToUpdate);
      alert('User data updated successfully!');
      setIsEditing(false);
    } catch (error) {
      console.error('Error updating user data:', error);
    }
  };

  return (
    <div className="container mt-5">
      <h2> </h2>
      {!isEditing ? (
        <div>
          <p><strong> :</strong> {userData.fullName}</p>
          <p><strong>:</strong> {userData.address}</p>
          <p><strong> :</strong> {userData.email}</p>
          <p><strong> :</strong> {userData.phoneNumber}</p>
         
          <button className="btn btn-primary" onClick={() => setIsEditing(true)}>
             
          </button>
        </div>
      ) : (
        <form>
          <div className="mb-3">
            <label className="form-label"> </label>
            <input
              type="text"
              className="form-control"
              name="fullName"
              value={userData.fullName}
              onChange={handleChange}
            />
          </div>
          <div className="mb-3">
            <label className="form-label"></label>
            <input
              type="text"
              className="form-control"
              name="address"
              value={userData.address}
              onChange={handleChange}
            />
          </div>
          <div className="mb-3">
            <label className="form-label"> </label>
            <input
              type="email"
              className="form-control"
              name="email"
              value={userData.email}
              onChange={handleChange}
            />
          </div>
          <div className="mb-3">
            <label className="form-label"> </label>
            <input
              type="text"
              className="form-control"
              name="phoneNumber"
              value={userData.phoneNumber}
              onChange={handleChange}
            />
          </div>
        
          <button type="button" className="btn btn-primary" onClick={handleUpdate}>
             
          </button>
        </form>
      )}
    </div>
  );
}

export default Userdata;
