import React from 'react';

export default function Footer() {
  const year = new Date().getFullYear();

  return (
    <footer className="bg-dark text-light py-3 d-flex flex-column align-items-center">
      <p className="mb-3"> {year} Rsidence Ramdane</p>
      <ul className="list-unstyled d-flex justify-content-center">
        <li className="mx-3">
          <a href="/" className="text-light">Home</a>
        </li>
        <li className="mx-3">
          <a href="/about" className="text-light">About Us</a>
        </li>
        <li className="mx-3">
          <a href="/contact" className="text-light">Contact</a>
        </li>
      </ul>
    </footer>
  );
}