import React, { useEffect, useState, useRef } from 'react';
import { getDatabase, ref, get, push, set } from 'firebase/database';
import { useParams, useNavigate } from 'react-router-dom';
import Carose from './Carose';
import { onAuthStateChanged } from 'firebase/auth';
import L from 'leaflet';
import { app, auth, database } from './firebase';

function PropertyDetailPage() {
  const navigate = useNavigate();
  const { propertyId } = useParams();
  const [property, setProperty] = useState(null);
  const [user, setUser] = useState(null);
  const [totalPrice, setTotalPrice] = useState(0);
  const [userRating, setUserRating] = useState(0);
  const [averageRating, setAverageRating] = useState(null);
  const [numberOfRatings, setNumberOfRatings] = useState(0);
  const mapRef = useRef(null);
  const mapInstance = useRef(null);

  useEffect(() => {
    const fetchData = async () => {
      const dbRef = ref(database, `properties/${propertyId}`);
      const snapshot = await get(dbRef);

      if (snapshot.exists()) {
        setProperty(snapshot.val());
        if (snapshot.val().location) {
          initMap(snapshot.val().location);
        }
      }

      const ratingsRef = ref(database, `ratings/${propertyId}`);
      const ratingsSnapshot = await get(ratingsRef);
      if (ratingsSnapshot.exists()) {
        const ratings = ratingsSnapshot.val();
        const totalRatings = Object.values(ratings);
        const sumRatings = totalRatings.reduce((acc, rating) => acc + rating, 0);
        setAverageRating((sumRatings / totalRatings.length).toFixed(1));
        setNumberOfRatings(totalRatings.length);
      }
    };

    fetchData();
  }, [propertyId]);

  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, (user) => {
      setUser(user);
    });

    return () => unsubscribe();
  }, []);

  const initMap = (location) => {
    if (!location || !location.latitude || !location.longitude) return;

    if (!mapInstance.current) {
      const map = L.map('map').setView([location.latitude, location.longitude], 13);
      L.tileLayer('https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', {
        attribution: '&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors',
      }).addTo(map);
      L.marker([location.latitude, location.longitude])
        .addTo(map)
        .bindPopup('Property Location')
        .openPopup();
      mapInstance.current = map;
    } else {
      mapInstance.current.setView([location.latitude, location.longitude], 13);
    }
  };

  const bookProperty = async (propertyId, userId, startDate, endDate) => {
    try {
      const bookingsRef = ref(database, 'bookings');
      const newBookingRef = push(bookingsRef);
      await set(newBookingRef, {
        propertyId,
        userId,
        startDate,
        endDate,
        etat: 'waiting',
      });
      console.log('Property booked successfully');
      return true;
    } catch (error) {
      console.error('Booking failed:', error.message);
      throw error;
    }
  };

  const handleBook = async () => {
    if (!user) {
      navigate("/register");
      return;
    }

    const startDateInput = document.getElementById('startDate').value;
    const endDateInput = document.getElementById('endDate').value;

    if (!startDateInput || !endDateInput) {
      alert('Please enter booking dates.');
      return;
    }

    const startDate = new Date(startDateInput);
    const endDate = new Date(endDateInput);
    const today = new Date();
    today.setHours(0, 0, 0, 0);

    if (endDate < startDate) {
      alert('End date cannot be before start date.');
      return;
    }

    if (startDate < today || endDate < today) {
      alert('Booking dates must be in the future.');
      return;
    }

    try {
      const bookingSuccess = await bookProperty(propertyId, user.uid, startDateInput, endDateInput);
      if (bookingSuccess) {
        navigate("/userdashbord/" + user.uid);
      }
    } catch (error) {
      console.error('Booking failed:', error.message);
    }
  };

  const calculatePrice = () => {
    const startDateInput = document.getElementById('startDate').value;
    const endDateInput = document.getElementById('endDate').value;
    const startDate = new Date(startDateInput);
    const endDate = new Date(endDateInput);

    if (isNaN(startDate.getTime()) || isNaN(endDate.getTime())) {
      setTotalPrice(0);
      return;
    }

    const oneDay = 24 * 60 * 60 * 1000;
    const numDays = Math.round(Math.abs((endDate - startDate) / oneDay));
    let totalPrice = 0;

    if (property) {
      const basePrice = parseFloat(property.price) || 0;
      const specialPrices = property.specialPrices || [];

      for (let i = 0; i < numDays; i++) {
        const currentDay = new Date(startDate);
        currentDay.setDate(startDate.getDate() + i);

        let pricePerDay = basePrice;

        specialPrices.forEach((specialPrice) => {
          const specialStartDate = new Date(specialPrice.startDate);
          const specialEndDate = new Date(specialPrice.endDate);
          if (currentDay >= specialStartDate && currentDay <= specialEndDate) {
            pricePerDay = parseFloat(specialPrice.price);
          }
        });

        totalPrice += pricePerDay;
      }
    }

    setTotalPrice(totalPrice);
  };

  const handleRatingChange = (event) => {
    setUserRating(parseInt(event.target.value));
  };

  const submitRating = async () => {
    if (!user) {
      alert('You must be logged in to rate.');
      return;
    }

    if (userRating < 1 || userRating > 5) {
      alert('Rating must be between 1 and 5.');
      return;
    }

    const ratingRef = ref(database, `ratings/${propertyId}/${user.uid}`);
    await set(ratingRef, userRating);
    alert('Rating submitted successfully!');
    setUserRating(0); // Reset rating after submission

    const ratingsRef = ref(database, `ratings/${propertyId}`);
    const ratingsSnapshot = await get(ratingsRef);
    if (ratingsSnapshot.exists()) {
      const ratings = ratingsSnapshot.val();
      const totalRatings = Object.values(ratings);
      const sumRatings = totalRatings.reduce((acc, rating) => acc + rating, 0);
      setAverageRating((sumRatings / totalRatings.length).toFixed(1));
      setNumberOfRatings(totalRatings.length);
    }
  };

  return (
    <div className="container mt-5">
      {property ? <Carose images={property.images} /> : ""}
      <div className="mt-3">
        <h2 id="propertyTitle">{property?.title}</h2>
        <p id="propertyDescription">{property?.description}</p>
        <input type="date" className="form-control" id="startDate" placeholder="Start Date" onChange={calculatePrice} />
        <input type="date" className="form-control" id="endDate" placeholder="End Date" onChange={calculatePrice} />
        <div id="priceDisplay">  : dzd {totalPrice}</div>
        <button id="bookButton" className="btn btn-primary" onClick={handleBook}> </button>

        <h3>  </h3>
        <div className="rating">
          <label>
            <input type="radio" value="1" checked={userRating === 1} onChange={handleRatingChange} /> 1
          </label>
          <label>
            <input type="radio" value="2" checked={userRating === 2} onChange={handleRatingChange} /> 2
          </label>
          <label>
            <input type="radio" value="3" checked={userRating === 3} onChange={handleRatingChange} /> 3
          </label>
          <label>
            <input type="radio" value="4" checked={userRating === 4} onChange={handleRatingChange} /> 4
          </label>
          <label>
            <input type="radio" value="5" checked={userRating === 5} onChange={handleRatingChange} /> 5
          </label>
        </div>
        <button className="btn btn-secondary" onClick={submitRating}>Submit Rating</button>

        {averageRating !== null && (
          <div className="average-rating mt-3">
            <h4>  : {averageRating}</h4>
            <p>  : {numberOfRatings}</p>
          </div>
        )}

        <div id="map" style={{ height: 400 }}></div>
      </div>
    </div>
  );
}

export default PropertyDetailPage;
