import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { getAuth, signInWithEmailAndPassword,GoogleAuthProvider, signInWithPopup  } from 'firebase/auth';
import {  ref, get,set } from "firebase/database";
import { database } from './firebase.js';
function LoginPage() {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState('');
  const navigate = useNavigate();

  const handleGoogleLogin = async () => {
    const auth = getAuth();
    const provider = new GoogleAuthProvider();

    try {
      await signInWithPopup(auth, provider);
      const user = auth.currentUser;
      if (user) {
        const usersRef = ref(database, 'users/' + user.uid);
        const snapshot = await get(usersRef);
        const userData = snapshot.val();

        // Save phone number if available
        const phoneNumber = user.phoneNumber;
        if (phoneNumber) {
          await set(usersRef, { ...userData, phoneNumber });
        }
       

        // Check user's role and redirect accordingly
        if (userData) {
          const role = userData.role;
          if (role === 'property_owner') {
            navigate('/dashbord'); // Redirect property owners to dashboard
          } else {
            navigate('/'); // Redirect other users to homepage
          }
        } else {
          // If no userData exists, initialize user data with phone number
          await set(usersRef, {email:user.email, role: 'renter', phoneNumber });
          navigate('/'); // Redirect new users to homepage
        }
      }
    } catch (error) {
      setError(error.message);
    }
  };




  const handleLogin =  async (even) => {
   
    const auth = getAuth();

    try {
      await signInWithEmailAndPassword(auth, email, password);
      const user = auth.currentUser;
      if (user) {
        const usersRef = ref(database, 'users/' + user.uid);
        const snapshot = await get(usersRef);
        const userData = snapshot.val();
        if (userData) {
          const role = userData.role;
          // Check user's role and redirect accordingly
          if (role === 'property_owner') {
            navigate('/dashbord') // Redirect property owners to dashboard
          } else {
              // Redirect other users to a different page (e.g., homepage)
              navigate("/") // Change 'index.html' to the desired URL
          }
      }
      }
      // Redirect to the homepage or dashboard after successful login
    } catch (error) {
      setError(error.message);
    }
  };

  return (
    <div className="login-container">
      <h1> </h1>
     
        <div>
          <label htmlFor="email"> </label>
          <input
            type="email"
            id="email"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            required
          />
        </div>
        <div>
          <label htmlFor="password"> </label>
          <input
            type="password"
            id="password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            required
          />
        </div>
        <button type="submit" onClick={handleLogin}></button>
        
        <button type="button" onClick={handleGoogleLogin}>  </button>
        {error && <p id="errorMessage" style={{ color: 'red' }}>{error}</p>}
     
    </div>
  );
}

export default LoginPage;