import React, { useState, useEffect } from 'react';
import { onAuthStateChanged, signOut } from 'firebase/auth';
import { Link, useNavigate } from 'react-router-dom';
import { auth,database as db } from './pages/firebase.js'; // Make sure db is imported
import { ref, get } from 'firebase/database'; // Import Realtime Database functions

function Header() {
  const [user, setUser] = useState(null);
  const [role, setRole] = useState('');
  const navigate = useNavigate();

  const login = () => {
    navigate("/login");
  };

  const registre = () => {
    navigate("/registe");
  };

  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, async (user) => {
      if (user) {
        setUser(user);
        const userRef = ref(db, 'users/' + user.uid);
        const userSnap = await get(userRef);
        if (userSnap.exists()) {
          setRole(userSnap.val().role);
        }
      } else {
        setUser(null);
        setRole('');
      }
    });

    return () => unsubscribe();
  }, []);

  const handleLogout = async () => {
    try {
      await signOut(auth);
      navigate("/");
    } catch (error) {
      console.error('Error signing out:', error.message);
    }
  };

  const loginButton = (
    <button
      id="loginBtn"
      className="btn btn-primary"
      onClick={login}
    >
      
    </button>
  );

  const registerButton = (
    <button
      id="registerBtn"
      className="btn btn-secondary"
      onClick={registre}
    >
      
    </button>
  );

  const logoutButton = (
    <button
      id="logoutBtn"
      className="btn btn-danger"
      onClick={handleLogout}
    >
      
    </button>
  );

  const controlPanelButton = (
    <button
      id="controlPanelBtn"
      className="btn btn-warning"
      onClick={() => navigate("/dashbord")}
    >
       
    </button>
  );
  const usercontrolPanelButton = (
    <button
      id="usercontrolPanelBtn"
      className="btn btn-warning"
      onClick={() => navigate('/userdashbord/'+user.uid)}
    >
       
    </button>
  );

  return (
    <nav className="navbar navbar-expand-lg navbar-dark bg-dark">
      <a className="navbar-brand" href="#">
        <img
          src="icon.jpg"
          alt="Icon"
          style={{ width: 30, height: 30 }}
          className="d-inline-block align-top mr-2"
        />
        Rsidence Ramdane
      </a>
      <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
        <span className="navbar-toggler-icon"></span>
      </button>
      <div className="collapse navbar-collapse" id="navbarSupportedContent">
        <ul className="navbar-nav mr-auto">
          <li className="nav-item">
            <Link className="nav-link" to="/"> </Link>
          </li>
          <li className="nav-item">
            <Link className="nav-link" to="/about"> </Link>
          </li>
        </ul>
        <div className="btn-group" role="group" aria-label="Authentication buttons">
          {!user && (
            <>
              {loginButton}
              {registerButton}
            </>
          )}
          {user && (
            <>
            {role === 'renter' && usercontrolPanelButton}
              {role === 'property_owner' && controlPanelButton}
              {logoutButton}
            </>
          )}
        </div>
      </div>
    </nav>
  );
}

export default Header;
