import React, { useState } from 'react';
import { storage, database, auth } from './firebase.js';
import { getStorage, ref as storageRef, uploadBytes, getDownloadURL } from 'firebase/storage';
import {  ref as databaseRef, get ,push} from "firebase/database";
function PropertyForm() {
  const [title, setTitle] = useState('');
  const [price, setPrice] = useState('');
  const [latitude, setLatitude] = useState('');
  const [longitude, setLongitude] = useState('');
  const [images, setImages] = useState([]);
  const [commun, setCommun] = useState('maghnia');
  const [description, setDescription] = useState('');
  const [specialPrices, setSpecialPrices] = useState([]);
  const [errorMessage, setErrorMessage] = useState('');

  const handleImageChange = (e) => {
    setImages(e.target.files);
  };

  const addSpecialPrice = () => {
    setSpecialPrices([...specialPrices, { startDate: '', endDate: '', price: '' }]);
  };

  const handleSpecialPriceChange = (index, key, value) => {
    const newSpecialPrices = [...specialPrices];
    newSpecialPrices[index][key] = value;
    setSpecialPrices(newSpecialPrices);
  };

  const removeSpecialPrice = (index) => {
    const newSpecialPrices = [...specialPrices];
    newSpecialPrices.splice(index, 1);
    setSpecialPrices(newSpecialPrices);
  };

  const handleSubmit = async () => {
    if (!title || !price || !latitude || !longitude || images.length === 0) {
      setErrorMessage('Please fill out all fields and upload at least one image.');
      return;
    }

    const property = {
      title,
      price,
      location: {
        latitude,
        longitude
      },
      commun,
      description,
      ownerId: auth.currentUser.uid,
      images: []
    };

    try {
      const uploadPromises = Array.from(images).map(imageFile => {
        const imageRef = storageRef(storage, 'images/' + imageFile.name);
        return uploadBytes(imageRef, imageFile).then(snapshot => getDownloadURL(snapshot.ref));
      });

      const imageUrls = await Promise.all(uploadPromises);
      property.images = imageUrls;
      property.image = imageUrls[0];
      property.specialPrices = specialPrices;

      await push(databaseRef(database, 'properties'), property);
     
    } catch (error) {
      setErrorMessage('Failed to add property. Please try again later.');
    }
  };

  return (
    <div class="modal" tabindex="-1" role="dialog" id="exampleModal">
    <div className="modal-content">
    <div class="modal-header d-flex justify-content-end">
        <h5 class="modal-title">  </h5>
        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
          <span aria-hidden="true">&times;</span>
        </button>
      </div>
      <div class="modal-body ">
     
    <form>
    <div className="form-group">
      <label> :</label>
      <input type="text" value={title} onChange={e => setTitle(e.target.value)} required />
    </div>
    <div className="form-group">
      <label> </label>
      <input type="number" value={price} onChange={e => setPrice(e.target.value)} required />
    </div>
    <div className="form-group">
      <label>Latitude:</label>
      <input type="text" value={latitude} onChange={e => setLatitude(e.target.value)} required />
    </div>
    <div className="form-group">
      <label>Longitude:</label>
      <input type="text" value={longitude} onChange={e => setLongitude(e.target.value)} required />
    </div>
    <div className="form-group">
      <label> </label>
      <textarea value={description} onChange={e => setDescription(e.target.value)} rows="4" cols="50"></textarea>
    </div>
    <div className="form-group">
      <label> :</label>
      <select value={commun} onChange={e => setCommun(e.target.value)}>
        <option value="maghnia">Maghnia</option>
        <option value="shaybe_rasou">Shaybe Rasou</option>
      </select>
    </div>
    <div className="form-group">
      <label></label>
      <input type="file" onChange={handleImageChange} accept="image/*" multiple required />
    </div>
    <div>
      <h4>  </h4>
      {specialPrices.map((sp, index) => (
        <div key={index} className="special-price-entry">
          <label>   </label>
          <input type="date" value={sp.startDate} onChange={e => handleSpecialPriceChange(index, 'startDate', e.target.value)} />
          <label>   </label>
          <input type="date" value={sp.endDate} onChange={e => handleSpecialPriceChange(index, 'endDate', e.target.value)} />
          <label> :</label>
          <input type="number" value={sp.price} onChange={e => handleSpecialPriceChange(index, 'price', e.target.value)} />
          <button type="button" onClick={() => removeSpecialPrice(index)}>Remove</button>
        </div>
      ))}
      <button type="button" onClick={addSpecialPrice}>  </button>
    </div>
    <button type="button" onClick={handleSubmit}>  </button>
    <p style={{ color: 'red' }}>{errorMessage}</p>
  </form></div>
</div></div>  );
}

export default PropertyForm;
