
import PropertyCard from './PropertyCard'; 
import {database} from './firebase.js';
import React, { useState, useEffect } from 'react';
import { getDatabase, ref, get ,remove} from 'firebase/database';


const Propertylist= (deleteo) => {
  const [properties, setProperties] = useState([]);
  useEffect(() => {
    const fetchData = async () => {
        const propertiesRef = ref(database,'properties');
      const snapshot = await get(propertiesRef);
      const propertyList = [];
      snapshot.forEach(childSnapshot => {
        const propertyId = childSnapshot.key;
        const propertyData = childSnapshot.val();
        propertyList.push({
          id: propertyId,
          deletable:deleteo.deleteo,
          ...propertyData
        });
      });
      
      setProperties(propertyList);

    }

      fetchData()
    }
  , []);
  const handleDelete = async (id) => {
    try {
      const propertyRef = ref(database, `properties/${id}`);
      await remove(propertyRef);
      setProperties(prevProperties => prevProperties.filter(property => property.id !== id));
    } catch (error) {
      console.error('Error deleting property:', error);
    }
  };
  return (
    <div className="container mt-4">
      <h2 className="mb-4"  style={{textAlign:'right'}}> </h2>
      <div className="row">
        {properties.map(property => (
          <PropertyCard  key={property.id}  onDelete={handleDelete} property={ property} />
        ))}
      </div>
    </div>
  );
};

export default Propertylist;